.form {
    display: flex;
    flex-direction: column;
    margin: 20px;
    border: 2px solid black;
    width: 200px;
    height: 120px;
    padding: 15px;
    align-items:flex-start;
    border-radius: 4px;
   
} 
.btn {
    width: 100px;
    margin-top: 5px;
    color: springgreen;
    font-weight: 600;
    border: none;
    background: none;
    cursor: pointer;
}

.input {
    width: 150px;
    height: 15px;
    background:lavender;
    border-radius: 4px;
}
.label {
    display: flex;
    flex-direction: column;
}
